import React from 'react';
import { Divider } from 'antd';
import { DivImg, IL } from './../Styled';

const SuccessCmp = props => {
  return (
    <React.Fragment>
      <DivImg>
        <IL
          src={`${process.env.PUBLIC_URL}/images/undraw_confirmation_2uy0.svg`}
          loading={() => <div>{props.messages.process.loading}...</div>}
          error={() => <div>Error</div>}
        />
      </DivImg>

      <h2 style={{ textAlign: 'center' }}>{props.messages.process.successProcess}</h2>
      <p style={{ textAlign: 'center' }}>{props.messages.process.successProcessSub}</p>

      <Divider />
    </React.Fragment>
  );
};

export default SuccessCmp;
