import React, { useState, useEffect } from 'react';
import {
  Row,
  Col,
  Tabs,
  Table,
  Tooltip,
  Modal,
  Tag,
  Button,
  Icon,
  message,
  Alert,
  Popconfirm,
} from 'antd';
import { Link } from 'react-router-dom';
import { injectIntl } from 'react-intl';

import fileDownload from 'js-file-download';
import axios from 'axios';

import { ButtonWrapper, BadgeWrapper, Span, ButtonAction } from './../Styled';
import SuccessCmp from './../SuccessCmp';
import ErrorCmp from './../ErrorCmp';
import functions from './../../utils/functions';
import auth from './../../utils/auth';
import config from './../../config';

import {
  getProcess,
  processFileAfterQuoteFile,
  removeProcess,
  setDownloaded,
  downloadFile,
  removeAllProcess,
} from './../../services';

const TabPane = Tabs.TabPane;

const ProcessList = props => {
  const {
    userLogin,
    intl: { messages },
  } = props;

  const [tab, setTab] = useState('no-finished');
  const [proceses, setProceses] = useState([]);
  const [bi, setBi] = useState(null);
  const [loading, setLoading] = useState(false);
  const [payResponse, setPayResponse] = useState(null);
  const [loadingPay, setLoadingPay] = useState(false);
  const [visible, setVisible] = useState(false);
  const [processSelected, setProcessSelected] = useState(null);
  const [dowloading, setDowloading] = useState(false);
  const [remloading, setRemloading] = useState(false);

  useEffect(() => {
    const CancelToken = axios.CancelToken;
    const source = CancelToken.source();

    fetchProceses(source);
    const polling = setInterval(() => {
      fetchProceses(source, true);
    }, 5000);

    return function cleanup() {
      clearInterval(polling);
      source.cancel('unmounted');
    };

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const fetchProceses = async (source, polling) => {
    try {
      if (!polling) setLoading(true);
      const response = await getProcess(source);
      let array = response.data.process.map((item, index) => {
        item.key = index;
        return item;
      });
      setBi(response.data.billingInformation);
      if (!remloading) setProceses(array);
      setLoading(false);
    } catch (error) {
      if (error.message !== 'unmounted') message.error(error.message);
      setDowloading(false);
      setLoading(false);
    }
  };

  const pay = async id => {
    setLoadingPay(id);
    document.getElementById(id).submit();
  };

  const processFile = async (id, selected, quote) => {
    setLoadingPay(id);
    try {
      await processFileAfterQuoteFile({ fileId: selected.fileId, quote });
      message.success(messages.default.success);
      setLoadingPay(false);
      hideModal();
      fetchProceses();
    } catch (error) {
      message.error(error.message);
    }
  };

  const showModal = processSelected => {
    setVisible(true);
    setProcessSelected(processSelected);
  };

  const hideModal = () => {
    setVisible(false);
    setPayResponse(null);
  };

  const download = async record => {
    try {
      setDowloading(record.id);

      if (record.fileDownload) {
        await setDownloaded({ id: record.id });
        var base64 = record.fileDownload.file;
        var type = record.fileDownload.fileType;
        var name = record.fileDownload.fileDownloadName || record.fileDownload.filename;
        window
          .FileDownload(base64, type, name)
          .then(() => console.log('download successful.'))
          .catch(err => console.error('download error: ', err));
      } else {
        const response = await downloadFile({ id: record.id });
        let sname = record.fileName.split('.');
        sname.pop();
        let name = sname.join('');
        let ext = record.fileDownloadName.split('.').pop();

        fileDownload(response.data, `${name}.${ext}`);
      }

      setDowloading(false);
    } catch (error) {
      setDowloading(false);
      message.error(error.message);
    }
  };

  const removed = async record => {
    try {
      const CancelToken = axios.CancelToken;
      const source = CancelToken.source();
      setRemloading(true);
      await removeProcess({ id: record.id });
      fetchProceses(source);
      setRemloading(false);
    } catch (error) {
      setRemloading(false);
      message.error(error.message);
    }
  };

  const removeAll = async record => {
    try {
      const CancelToken = axios.CancelToken;
      const source = CancelToken.source();
      setLoading(true);
      await removeAllProcess({ status: tab });
      fetchProceses(source);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      message.error(error.message);
    }
  };

  const changeTab = key => {
    setTab(key);
  };

  const calculatePrice = price => {
    let applyTax = false;

    if (bi && bi.continent === 'EU') {
      applyTax = true;
    }

    let aux = price.toFixed(2);
    if (aux === '0.00') aux = '0.01';

    if (applyTax) {
      const tax = ((parseFloat(aux) * 21) / 100).toFixed(2);
      aux = parseFloat(aux) + parseFloat(tax);
    }
    return aux;
  };

  const columns = [
    {
      title: messages.process.fileName,
      dataIndex: 'fileName',
      key: 'fileName',
      sorter: (a, b) => a.fileName - b.fileName,
    },
    {
      title: messages.process.processName,
      dataIndex: 'processName',
      key: 'processName',
      render: (text, record) => (
        <span style={{ textTransform: 'capitalize' }}>{record.processName}</span>
      ),
    },
    {
      title: messages.process.subProcess,
      dataIndex: 'processType',
      key: 'processType',
      render: (text, record) => {
        let value = '---';
        if (text && text !== record.processName) {
          value = text;
        }
        return <span style={{ textTransform: 'capitalize' }}>{value}</span>;
      },
    },
    {
      title: messages.process.language,
      key: 'language',
      render: (text, record) => (
        <span style={{ textTransform: 'capitalize' }}>
          <Tag style={{ cursor: 'default' }} color="#108ee9">
            {record.engineSource}
          </Tag>{' '}
          <Icon type="arrow-right" />{' '}
          <Tag style={{ cursor: 'default' }} color="#ff3f1b">
            {record.engineTarget}
          </Tag>
        </span>
      ),
    },
    {
      title: messages.process.status,
      dataIndex: 'status',
      key: 'status',
      render: (text, record) => {
        if (!(record.quoteSelected && record.quoteSelected.price)) {
          return <Span>{functions.getStatusByText(record.status)}</Span>;
        } else {
          return <Span>{messages.process.paidProcess}</Span>;
        }
      },
      filters: [
        {
          text: 'Waiting',
          value: 'waiting',
        },
        {
          text: 'Waiting Quoting',
          value: 'waiting_quoting',
        },
        {
          text: 'Quotes Ready',
          value: 'quotes_ready',
        },
        {
          text: 'Pending Accept/Reject',
          value: 'pending_accept_reject',
        },
        {
          text: 'Preprocessing',
          value: 'preprocessing',
        },
        {
          text: 'Processing',
          value: 'processing',
        },
        {
          text: 'PostProcessing',
          value: 'postProcessing',
        },
      ],
      onFilter: (value, record) => record.status === value,
    },
    {
      title: messages.process.quote,
      dataIndex: 'quoteSelected',
      key: 'quotes',
      render: (text, record) => {
        return (
          <span>
            {record.quoteSelected ? (
              <div>
                <Icon type="euro" /> &nbsp;
                {record.quoteSelected.price}
              </div>
            ) : (
              '---'
            )}
          </span>
        );
      },
    },
    {
      title: messages.default.actions,
      key: 'action',
      render: (text, record) => (
        <span>
          <Tooltip placement="top" title="See Quotes">
            <ButtonAction
              onClick={() => {
                showModal(record);
              }}
              disabled={record.status !== 'quote_ready' || record.quoteSelected}
              size="small"
              type="primary"
              shape="circle"
              icon="wallet"
            />
          </Tooltip>
          <Popconfirm
            title={messages.process.confirm}
            onConfirm={() => {
              removed(record);
            }}
            okText={messages.default.yes}
            cancelText={messages.default.cancel}
          >
            <ButtonAction
              onClick={() => {}}
              disabled={record.status <= 100}
              size="small"
              type="danger"
              shape="circle"
              icon="delete"
            />
          </Popconfirm>
        </span>
      ),
    },
  ];
  const columns1 = [
    {
      title: messages.process.fileName,
      dataIndex: 'fileName',
      key: 'fileName',
      sorter: (a, b) => a.fileName - b.fileName,
    },
    {
      title: messages.process.processName,
      dataIndex: 'processName',
      key: 'processName',
      render: (text, record) => (
        <span style={{ textTransform: 'capitalize' }}>{record.processName}</span>
      ),
    },
    {
      title: messages.process.processType,
      dataIndex: 'processType',
      key: 'processType',
      render: (text, record) => {
        let value = '---';
        if (text && text !== record.processName) {
          value = text;
        }
        return <span style={{ textTransform: 'capitalize' }}>{value}</span>;
      },
    },
    {
      title: messages.process.language,
      key: 'language',
      render: (text, record) => (
        <span style={{ textTransform: 'capitalize' }}>
          <Tag color="#108ee9">{record.engineSource}</Tag> <Icon type="arrow-right" />{' '}
          <Tag color="#ff3f1b">{record.engineTarget}</Tag>
        </span>
      ),
    },
    {
      title: messages.process.status,
      dataIndex: 'status',
      key: 'status',
      render: status => <Span>{functions.getStatusByText(status)}</Span>,
    },
    {
      title: messages.process.quote,
      dataIndex: 'quoteSelected',
      key: 'quotes',
      render: (text, record) => {
        return (
          <span>
            {record.quoteSelected ? (
              <div>
                <Icon type="euro" /> &nbsp;
                {record.quoteSelected.price}
              </div>
            ) : (
              '---'
            )}
          </span>
        );
      },
    },
    {
      title: messages.default.actions,
      key: 'action',
      render: (text, record) => (
        <span>
          <Tooltip placement="top" title={messages.process.download}>
            <ButtonAction
              onClick={() => {
                download(record);
              }}
              loading={dowloading === record.id}
              disabled={record.status <= 100}
              size="small"
              type="primary"
              shape="circle"
              icon="download"
            />
          </Tooltip>
          {record.downloaded && (
            <Popconfirm
              title={messages.process.confirm}
              onConfirm={() => {
                removed(record);
              }}
              okText={messages.default.yes}
              cancelText={messages.default.cancel}
            >
              <ButtonAction
                onClick={() => {}}
                disabled={record.status <= 100}
                size="small"
                type="danger"
                shape="circle"
                icon="delete"
              />
            </Popconfirm>
          )}
        </span>
      ),
    },
  ];

  let finished = [],
    inProcess = [];

  if (proceses && proceses.length) {
    finished = proceses.filter(item => {
      return item.status === 'downloaded' || item.status === 'finished';
    });

    inProcess = proceses.filter(item => {
      return !(item.status === 'downloaded' || item.status === 'finished');
    });
  }

  const operations = (
    <React.Fragment>
      <Popconfirm title="Are you sure？" okText="Yes" cancelText="No" onConfirm={removeAll}>
        <ButtonWrapper className="ml-2" icon="delete" style={{ float: 'right' }} type="danger">
          Clear All
        </ButtonWrapper>
      </Popconfirm>

      <Link to="/dashboard/process-services/new">
        <ButtonWrapper loading={loading} type="primary" icon="file-add">
          {messages.process.newProcess}
        </ButtonWrapper>
      </Link>
    </React.Fragment>
  );

  return (
    <div>
      <Row>
        <Col>
          <Tabs defaultActiveKey={tab} tabBarExtraContent={operations} onChange={changeTab}>
            <TabPane
              tab={
                <BadgeWrapper count={inProcess.length}>
                  <span>{messages.process.inProcess}</span>
                </BadgeWrapper>
              }
              key="no-finished"
            >
              <Row>
                <Table
                  key="table1"
                  size="small"
                  loading={loading || remloading}
                  columns={columns}
                  dataSource={inProcess}
                />
              </Row>
            </TabPane>
            <TabPane
              tab={
                <BadgeWrapper count={finished.length}>
                  <span>{messages.process.finished}</span>
                </BadgeWrapper>
              }
              key="finished"
            >
              <Table
                key="table2"
                size="small"
                loading={loading || remloading}
                columns={columns1}
                dataSource={finished}
              />
            </TabPane>
          </Tabs>
        </Col>
      </Row>

      <Modal title="Quotes" width={600} visible={visible} onCancel={hideModal} footer={null}>
        {payResponse !== 'success' && payResponse !== 'error' ? (
          <Row justify="center" type="flex">
            {processSelected && processSelected.quotes
              ? processSelected.quotes.map((item, index) => {
                  return (
                    <Col key={item.optionid} xs={24} md={8}>
                      <Row justify="center" type="flex" align="middle">
                        <Col
                          style={{
                            textAlign: 'center',
                            minHeight: 45,
                          }}
                        >
                          <h4 style={{ minHeight: 41 }}>{item.option}</h4>

                          <form
                            hidden
                            id={`quote${index}`}
                            action={`${config[config.default].hostApi}/api/payment/register/pay`}
                            method="post"
                          >
                            <input type="text" name="token" value={auth.getToken()} />
                            <input type="text" name="billing" value={JSON.stringify(bi)} />
                            <input type="text" name="uuid" value={processSelected.uuid} />
                            <input type="text" name="quote" value={item.optionid} />
                            <input
                              type="text"
                              name="hostClient"
                              value={`${config[config.default].hostClient}`}
                            />
                          </form>

                          <Button
                            loading={loadingPay === `quote${index}`}
                            onClick={() => {
                              userLogin.typeOfUser === 3
                                ? pay(`quote${index}`)
                                : processFile(`quote${index}`, processSelected, item);
                            }}
                            style={{ marginTop: 16 }}
                            type="primary"
                          >
                            <React.Fragment>
                              &nbsp;&nbsp;
                              <Icon type="euro" /> {calculatePrice(item.price)}
                            </React.Fragment>
                          </Button>
                        </Col>
                      </Row>
                    </Col>
                  );
                })
              : null}
            {bi && bi.continent === 'EU' && (
              <div style={{ marginTop: 20 }}>
                <Alert message="Tax Include" type="info" showIcon />
              </div>
            )}
          </Row>
        ) : null}
        {payResponse === 'success' ? <SuccessCmp messages={messages} /> : null}
        {payResponse === 'error' ? <ErrorCmp messages={messages} /> : null}
      </Modal>
    </div>
  );
};

export default injectIntl(ProcessList);
