/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from 'react';

import ProcessList from './../ProcessList';

import './index.css';
import { Card } from 'antd';

require('./file-download');

const ProcessServicesCmp = props => {
  const { userLogin, setCollapsed } = props;

  useEffect(() => {
    return function cleanup() {
      setCollapsed(false);
    };
  }, []);

  return (
    <div>
      {props.children}
      <Card>
        <ProcessList userLogin={userLogin} />
      </Card>
    </div>
  );
};

export default ProcessServicesCmp;
