import React from 'react';
import { Divider } from 'antd';
import { DivImg, IL } from './../Styled';

const ErrorCmp = props => (
  <React.Fragment>
    <DivImg>
      <IL
        src={`${process.env.PUBLIC_URL}/images/undraw_bug_fixing_oc7a.svg`}
        loading={() => <div>{props.messages.process.loading}...</div>}
        error={() => <div>Error</div>}
      />
    </DivImg>

    <h2> {props.messages.process.errorProcess}</h2>
    <p>{props.messages.process.errorProcessSub}</p>
    <Divider />
  </React.Fragment>
);

export default ErrorCmp;
